.header {
  @apply flex justify-between pl-1 pr-2 mb-20;
}
.header .hidden {
  display: none;
}
.header .line {
  border: 0.125rem solid var(--very-dark-blue);
  height: 0.0625rem;
  width: 1.375rem;
  display: flex;
  margin-bottom: 0.25rem;
}
.header__burger {
  @apply cursor-pointer;
  transition: 0ms ease-in-out;
  width: 1.375rem;
  z-index: 1000;
}
.header__burger:hover {
  transform: scale(1.1);
}
@media (max-width: 62em) {
  .header__menu--options {
    position: absolute;
    display: flex;
    gap: 16px;
    flex-direction: column;
    left: -17rem;
    bottom: -7rem;
    z-index: 999;
    background: black;
    padding: 40rem;
  }
}
@media (min-width: 63em) {
  .header {
    @apply justify-around items-center;
  }
  .header__burger {
    display: none;
  }
  .header .hidden {
    @apply flex flex-row gap-4 items-center;
  }
  .header__menu--options {
    @apply cursor-pointer;
  }
  .header__menu--options:hover {
    color: var(--Soft-red);
  }
  .header__menu button {
    @apply p-3 cursor-pointer shadow-lg font-bold;
    border-radius: 0.3125rem;
    text-decoration: none;
    border: none;
    color: var(--white);
    background-color: var(--Soft-red);
    letter-spacing: 0.1875rem;
    text-transform: uppercase;
    transition: 150ms ease-in-out;
  }
  .header__menu button:hover {
    transform: scale(1.1);
    border: 2px solid var(--Soft-red);
    background-color: var(--white);
    color: var(--Soft-red);
  }
}

.main__part1 {
  @apply flex flex-col items-center mt-10 mb-32;
}
.main__part1 .img1 {
  max-width: 50%;
}
.main__part1 .manager {
  margin-top: 3.75rem;
  max-width: 18.75rem;
}
.main__part1 .soft-blue {
  position: relative;
  bottom: 37rem;
  left: 12.688rem;
  background-color: var(--Soft-blue);
  border: 0.125rem solid var(--Soft-blue);
  border-radius: 6.25rem;
  display: flex;
  height: 12rem;
  margin-bottom: -11.25rem;
  width: 38.4375rem;
  z-index: -1;
}
.main__part1 .box {
  @apply flex flex-row justify-center gap-4 mt-8;
}
.main__part1 button {
  @apply p-4 cursor-pointer shadow-lg font-bold;
  border-radius: 0.3125rem;
  text-decoration: none;
  border: none;
  transition: 150ms ease-in-out;
}
.main__part1 .blue {
  color: var(--white);
  background-color: var(--Soft-blue);
}
.main__part1 .blue:hover {
  transform: scale(1.1);
  border: 2px solid var(--Soft-blue);
  background-color: var(--white);
  color: var(--Soft-blue);
}
.main__part1 .white {
  color: var(--very-dark-blue);
}
.main__part1 .white:hover {
  transform: scale(1.1);
  border: 2px solid var(--very-dark-blue);
  background-color: var(--white);
}
@media (min-width: 39.125em) {
  .main__part1 .position {
    display: grid;
    grid-template-columns: 1fr 2fr 3fr;
  }
  .main__part1 .img1 {
    max-width: 75%;
    grid-column: 3;
    position: relative;
    left: 6.25rem;
    margin-bottom: 1rem;
  }
  .main__part1 .soft-blue {
    background-color: var(--Soft-blue);
    border: 0.125rem solid var(--Soft-blue);
    border-radius: 6.25rem;
    bottom: calc(32.813rem + 1vw);
    display: flex;
    height: calc(15rem + 4vw);
    left: calc(-0.187rem + 47vw);
    margin-bottom: -11.25rem;
    position: absolute;
    width: 59.438rem;
    z-index: -1;
  }
  .main__part1 .manager {
    margin-top: 12.5rem;
    display: contents;
  }
  .main__part1 p {
    max-width: 31.25rem;
  }
}
@media (min-width: 63em) {
  .main__part1 {
    @apply grid grid-cols-2;
  }
  .main__part1 .manager {
    @apply flex justify-center items-center flex-col;
    grid-column: 1;
    margin-top: 0;
    margin-top: -25rem;
    margin-left: 9rem;
    position: initial;
  }
  .main__part1 .soft-blue {
    background-color: var(--Soft-blue);
    border: 0.125rem solid var(--Soft-blue);
    border-radius: 6.25rem;
    bottom: 16rem;
    left: 11rem;
    display: flex;
    height: calc(15rem + 4vw);
    margin-bottom: -11.25rem;
    position: absolute;
    width: 59.438rem;
    z-index: -1;
  }
  .main__part1 .img1 {
    grid-column: 2;
    max-width: 100%;
    position: inherit;
  }
  .main__part1 h1 {
    text-align: start;
    justify-content: start;
  }
  .main__part1 p {
    text-align: start;
  }
}

.main__part2 {
  @apply mb-5;
}
.main__part2 .hr2 {
  display: none;
}
.main__part2 hr {
  position: relative;
  bottom: 0.1875rem;
  z-index: -1;
}
.main__part2 p {
  max-width: 37.5rem;
}
.main__part2 h3 {
  @apply cursor-pointer;
  margin-top: -1.4375rem;
  margin-bottom: -1.4375rem;
  padding: 1.125rem;
  text-align: center;
}
.main__part2 h3:hover {
  margin-left: 6.5625rem;
  position: relative;
  right: 3.125rem;
  color: var(--Soft-red);
}
.main__part2 .focused {
  border-bottom: 0.4375rem solid var(--Soft-red);
}
@media (min-width: 63em) {
  .main__part2 .hr1 {
    display: none;
  }
  .main__part2 .b2 {
    @apply flex flex-row mt-20;
  }
  .main__part2 .hr2 {
    @apply flex;
  }
}

.main__part3 {
  @apply mt-10 mb-28 flex flex-col items-center;
}
.main__part3 .img2 {
  max-width: 50%;
}
.main__part3 .mp2 {
  max-width: 300px;
}
.main__part3 .manager {
  margin-top: 8.75rem;
  max-width: 28.5rem;
  min-width: 6.25rem;
}
.main__part3 .blue {
  @apply cursor-pointer mt-10 ml-2;
  padding: 0.625rem;
  border-radius: 0.3125rem;
  color: var(--white);
  background-color: var(--Soft-blue);
  border: none;
}
.main__part3 .blue:hover {
  transform: scale(1.1);
  border: 2px solid var(--Soft-blue);
  background-color: var(--white);
  color: var(--Soft-blue);
}
.main__part3 .soft-blue2 {
  background-color: var(--Soft-blue);
  border: 0.125rem solid var(--Soft-blue);
  border-radius: 6.25rem;
  bottom: 30.813rem;
  display: flex;
  height: 15rem;
  left: -13.312rem;
  margin-bottom: -11.25rem;
  position: relative;
  width: 38.4375rem;
  z-index: -1;
}
@media (min-width: 39.125em) {
  .main__part3 .soft-blue2 {
    background-color: var(--Soft-blue);
    border: 0.125rem solid var(--Soft-blue);
    border-radius: 6.25rem;
    bottom: calc(-41.187rem + 1vw);
    display: flex;
    height: calc(18rem + 4vw);
    left: calc(-45.312rem + 47vw);
    margin-bottom: -11.25rem;
    position: absolute;
    width: 59.438rem;
    z-index: -1;
  }
  .main__part3 .img2 {
    max-width: 100%;
  }
}

@media (min-width: 63em) {
  .main__part3 {
    @apply flex-row;
    width: auto;
  }
  .main__part3 .manager {
    margin-top: 0.25rem;
    margin-left: 1.25rem;
    position: relative;
    left: calc(-12.5rem + 20vw);
    top: 1.4375rem;
  }
  .main__part3 .mh2 {
    text-align: start;
    justify-content: start;
  }
  .main__part3 .mp2 {
    text-align: start;
  }
  .main__part3 .soft-blue2 {
    background-color: var(--Soft-blue);
    border: 0.125rem solid var(--Soft-blue);
    border-radius: 32.25rem;
    bottom: -28.8rem;
    display: flex;
    height: 21rem;
    left: calc(-59.312rem + 30vw);
    margin-bottom: -11.25rem;
    position: absolute;
    width: 73.438rem;
    z-index: -1;
  }
}
.hide {
  display: none;
}

.main__part4 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main__part4 .blue {
  @apply cursor-pointer mt-10;
  padding: 1.125rem;
  border-radius: 0.3125rem;
  color: var(--white);
  background-color: var(--Soft-blue);
  border: none;
}
.main__part4 .blue:hover {
  transform: scale(1.1);
  border: 2px solid var(--Soft-blue);
  background-color: var(--white);
  color: var(--Soft-blue);
}
.main__part4--box4 {
  max-width: 41.875rem;
}
.main__part4--card {
  @apply flex flex-col items-center mt-20 shadow-lg p-6;
}
.main__part4 .img4 {
  margin-left: 0.4375rem;
  max-width: 7.5rem;
}
@media (min-width: 31.5em) {
  .main__part4 .blue {
    margin-left: 0.4375rem;
  }
}
@media (min-width: 32.125em) {
  .main__part4--card {
    width: 19.4375rem;
  }
}
@media (min-width: 63em) {
  .main__part4 {
    margin-top: 8rem;
  }
  .main__part4 .cards {
    @apply flex flex-row gap-8;
  }
  .main__part4 .first {
    position: relative;
    bottom: 3.125rem;
  }
  .main__part4 .third {
    position: relative;
    top: 3.125rem;
  }
}

.main__part5 {
  @apply flex justify-center flex-col mt-36 mb-36;
}
.main__part5 .hide {
  display: none;
}
.main__part5 .container {
  @apply ml-6 mr-10 cursor-pointer;
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  transition: 150ms ease-in-out;
  position: relative;
  transition: background-image 150ms ease;
}
.main__part5 .container .arrow {
  grid-column: 4;
  height: 1.25rem;
}
.main__part5 .container:hover .arrow {
  width: 3.75rem;
  margin-bottom: 1.4375rem;
  background-image: url(../../../app/images/icon-arrow__hover.svg);
}
.main__part5 .container:hover .question {
  color: var(--Soft-red);
}
.main__part5 .img5 {
  @apply h-4 w-7;
  top: 0;
  opacity: 1;
  z-index: 1;
}
.main__part5 .container:hover .img5 {
  opacity: 0; /* Hide the first image on hover */
  transform: rotateX(180deg);
}
.main__part5 .answer {
  margin-top: 1.25rem;
  text-align: start;
}
.main__part5 .blue {
  @apply cursor-pointer mt-10 ml-2;
  padding: 1.125rem;
  border-radius: 0.3125rem;
  color: var(--white);
  background-color: var(--Soft-blue);
  border: none;
  margin-left: auto;
  margin-right: auto;
}
.main__part5 .blue:hover {
  transform: scale(1.1);
  border: 2px solid var(--Soft-blue);
  background-color: var(--white);
  color: var(--Soft-blue);
}
.main__part5 .container2 {
  @apply ml-6 mr-6;
}
@media (min-width: 40.5em) {
  .main__part5 {
    display: flex;
    justify-content: center;
    width: 40.5rem;
  }
}

.footer .input-container {
  position: relative;
  margin-bottom: 1em;
}
.footer .error-message {
  padding: 0.4375rem;
  color: var(--white);
  background-color: var(--Soft-red);
  font-size: 0.9em;
  margin-top: 0;
  margin-left: 19px;
  border-radius: 5px;
  display: none; /* Initially hidden */
}
.footer .hide {
  display: none;
}
.footer__input.invalid {
  border: 2px solid var(--Soft-red);
  background-image: url("./../../../app/images/icon-error.svg");
  background-repeat: no-repeat;
  box-sizing: border-box;
  background-position: 12.5rem center; /* Adjust the position of the image */
}
.footer__joined {
  @apply text-white;
  letter-spacing: 0.3125rem;
  margin-top: 2.5rem;
  margin-bottom: -1.25rem;
  text-align: center;
  font-size: 0.9375rem;
}
.footer__h2 {
  @apply text-white font-bold text-[24px];
}
.footer .together-1 {
  display: flex;
  flex-direction: column;
}
.footer__input {
  color: var(--grayish-blue);
  padding: 0.9375rem 0;
  padding-left: 1.25rem;
  border: none;
  border-radius: 0.3125rem;
  margin: auto 1.25rem;
  border: 1px solid #ccc;
  padding: 0.5em;
}
.footer__button {
  @apply cursor-pointer;
  margin-top: 0.9375rem;
  margin-bottom: 2.5rem;
  padding: 0.9375rem;
  color: var(--white);
  background-color: var(--Soft-red);
  border: none;
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
  transition: 200ms ease-in-out;
}
.footer__button:hover {
  color: var(--Soft-red);
  background-color: var(--white);
  border: 3px solid var(--Soft-red);
}
.footer__part2 {
  @apply text-white;
}
.footer__logo {
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
}
.footer__options {
  @apply flex flex-col justify-center gap-7 ml-4;
  text-align: center;
}
.footer__options a:hover {
  color: var(--Soft-red);
}
.footer__social {
  @apply flex gap-10 ml-10 cursor-pointer;
  margin-bottom: 3.125rem;
}
.footer .change1 {
  position: relative;
  width: 100%;
  max-width: 1.5625rem;
  cursor: pointer;
  transition: background-image 150ms ease;
}
.footer .change1:hover {
  background-image: url(../../../app/images/icon-facebook-hover.svg);
  transform: scale(1.1);
  height: 1.5625rem;
  margin-bottom: -1.5625rem;
}
.footer__facebook {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
  z-index: 1;
  transition: opacity 300ms ease;
}
.change1:hover .footer__facebook {
  opacity: 0; /* Hide the first image on hover */
}
.footer .change2 {
  position: relative;
  width: 100%;
  max-width: 1.875rem;
  cursor: pointer;
  transition: background-image 150ms ease;
}
.footer .change2:hover {
  background-image: url(../../../app/images/icon-twitter-hover.svg);
  transform: scale(1.4);
  margin-left: 0.25rem;
  margin-top: 0.125rem;
  margin-bottom: -1.375rem;
}
.footer__twitter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
  z-index: 1;
  transition: opacity 300ms ease;
}
.change2:hover .footer__twitter {
  opacity: 0%; /* Hide the first image on hover */
}
.footer .wrapper1 {
  background-color: var(--Soft-blue);
  @apply grid justify-center p-3;
}
.footer .wrapper2 {
  @apply grid justify-center p-5 gap-7;
  background-color: var(--very-dark-blue);
}
@media (min-width: 48em) {
  .footer .error-message {
    width: 14.75rem;
  }
  .footer .together-1 {
    display: flex;
    flex-direction: row;
    margin-left: 2.125rem;
  }
  .footer__input {
    margin-top: 0;
    margin-right: -0.3125rem;
  }
  .footer__button {
    margin-top: 0;
  }
  .footer__button:hover {
    margin-top: -0.25rem;
  }
  .footer .together {
    grid-row: 1;
    grid-column: 1;
    display: flex;
    align-items: center;
    flex-direction: row;
  }
  .footer__logo {
    margin-top: 0;
    margin-bottom: 0;
  }
  .footer__options {
    flex-direction: row;
  }
  .footer__social {
    height: 0.625rem;
    margin: 0;
    grid-row: 1;
    grid-column: 4;
  }
  .footer__twitter {
    margin-bottom: -12.5rem;
  }
  .footer .wrapper2 {
    @apply flex justify-between;
    @apply grid grid-rows-1;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  list-style: none;
  font-size: 1.125rem;
  color: var(--grayish-blue);
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  font-family: "Rubik", sans-serif;
  margin-bottom: 200px;
  /*how much to scroll*/
}

body.no-scroll {
  overflow: hidden;
}

header,
main {
  margin: 1.875rem;
  color: var(--very-dark-blue);
}

main {
  @apply flex flex-col items-center;
}
main button {
  font-weight: bold;
}

footer {
  margin: 0;
}

img {
  max-width: 100%;
}

h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  color: var(--very-dark-blue);
}

h1 {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2.5rem;
  color: var(--very-dark-blue);
}

h2 {
  @apply font-bold;
  font-size: 1.75rem;
  color: var(--very-dark-blue);
  margin-top: 40px;
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
  text-align: center;
}

h3 {
  font-weight: 300;
  font-size: 1.25rem;
}

p {
  text-align: center;
  color: var(--grayish-blue);
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: var(--grayish-blue);
}

hr {
  text-align: center;
  margin: 1.4375rem;
  border: 1px solid var(--grayish-blue);
  width: max;
}

:root {
  --Soft-blue: hsl(231, 69%, 60%);
  --Soft-red: hsl(0, 94%, 66%);
  --grayish-blue: hsl(229, 8%, 60%);
  --very-dark-blue: hsl(229, 31%, 21%);
  --white: hsl(0, 0%, 100%);
}